.Converter {
  background-color: rgb(167, 168, 168);
  width: 300px;
  height: 300px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 1rem;
  margin-top: 2rem;
}

.forms {
  display: flex;
  flex-direction: row;
}

.container {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

.inputBox {
  width: fit-content;
}

.resultText {
  font-size: 2rem;
  height: 40px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
